<template>
    <div class="contentTitle">
        <a-tabs @change="tabChange" :activeKey="defaultKey">
            <a-tab-pane key="article">
                <span slot="tab">
                    <a-icon type="apple" />
                    设计文章
                </span>
                <contentList :pageAlldata="articleData" :pageShowData="pageArticleData" :len="articleDataLen"></contentList>
                <!-- <div>
                    <router-view ></router-view>
                </div> -->
            </a-tab-pane>
            <a-tab-pane key="frontend">
                <span slot="tab">
                    <a-icon type="android" />
                    前端文章
                </span>
                <contentList :pageAlldata="frontendData" :pageShowData="pageFrontendData" :len="frontendDataLen"></contentList>
                <!-- <div>
                    <router-view></router-view>
                </div> -->
            </a-tab-pane>
            <a-tab-pane key="travel">
                <span slot="tab">
                    <a-icon type="android" />
                    旅游杂记
                </span>
                <contentList :pageAlldata="travelData" :pageShowData="pageTravelData" :len="travelDataLen"></contentList>
                <!-- <div>
                    <router-view></router-view>
                </div> -->
            </a-tab-pane>
        </a-tabs>
        
    </div>
</template>

<script>
import axios from 'axios'
import contentList from '@/components/home/contentList'
export default {
    name:'HomeContent',
    data() {
        return {
            getAllData:null, //所有显示数据
            tabAllData:null, //每个tab要显示的数据
            pageAlldata:null, //每个页面要显示的数据
            articleData:[],
            frontendData:[],
            travelData:[],
            pageArticleData:[],
            pageFrontendData:[],
            pageTravelData:[],
            articleDataLen:'',
            frontendDataLen:'',
            travelDataLen:'',
            defaultKey: this.$store.state.defaultKey
        }
    },
    components:{
        contentList
    },
    methods: {
        tabChange(activeKey){
            // this.$store.dispatch('getChangeFlag',activeKey)
            // this.$store.state.flag = activeKey
            // console.log('contentTitle:'+activeKey)
            // this.tabAllData=this.getAllData.filter((item,index)=>{
            //     return item.kind == activeKey
            // })
            // this.pageShowDataFun()
            this.defaultKey = activeKey
            this.$store.dispatch('getDefaultKey',activeKey)
            if(activeKey == 'article'){
                localStorage.setItem('detailData',JSON.stringify(this.articleData))
            }else if(activeKey == 'frontend'){
                localStorage.setItem('detailData',JSON.stringify(this.frontendData))
            }else if(activeKey == 'travel'){
                localStorage.setItem('detailData',JSON.stringify(this.travelData))
            }
            
        },
        getAllArticle(){
           return axios.get('/fxy/allArticle')
        },
        pageShowDataFun(data,key){
            const pages=[]
            data.forEach((item,index)=>{
                
                const page=Math.floor(index/4)
                if(!pages[page]){
                    pages[page]=[]
                }
                pages[page].push(item)
            });
            if(key =='article'){
                this.pageArticleData =pages
            }else if(key =='frontend'){
                this.pageFrontendData =pages
            }else if(key =='travel'){
                this.pageTravelData =pages
            }
        },
    },
    beforeMount() {
        this.getAllArticle()
        .then((res)=>{
            if(res.status == 200){
                this.getAllData = res.data
                this.articleData=this.getAllData.filter((item,index)=>{ //第一次默认key为article
                    return item.kind == '设计文章'
                })
                this.articleDataLen = this.articleData.length
                this.pageShowDataFun(this.articleData,'article')
                localStorage.setItem('detailData',JSON.stringify(this.articleData))
                this.frontendData=this.getAllData.filter((item,index)=>{ //第一次默认key为article
                    return item.kind == '前端文章'
                })
                this.frontendDataLen= this.frontendData.length
                this.pageShowDataFun(this.frontendData,'frontend')
                this.travelData=this.getAllData.filter((item,index)=>{ //第一次默认key为article
                    return item.kind == '旅游杂记'
                })
                this.travelDataLen = this.travelData.length
                this.pageShowDataFun(this.travelData,'travel')
                // this.pageShowDataFun()
            }else{
                alert('服务端调用异常')
            }
        }).
        catch((res)=>{
            this.$message.error('Request failed with status code 504');
        })
    },
    computed: {
        // allTotal(){
        //     // if(this.tabAllData)this.tabAllData= []
        //     return this.tabAllData.length
        // }
    },
}
</script>

<style scoped>
.contentTitle{
    padding:50px 0px;
    margin-left:20%;
}
a{
    text-decoration: none;
    color:#000;
    font-size: 16px;
    /* margin:0px 10px; */
    /* padding:0px 10px; */
    padding-left: 10px;
}
a::after{
    content:'|';
    color:#999;
    /* background:red;
    width:1px;
    height: 100%; */
    padding-left: 10px;
}
a:last-child::after{
    content:'';
}
a:hover{
    color:#ff7f21;
}
a:first-child{
    padding:0px;
    color:#ff7f21;
}
@media screen and (max-width: 768px){
    .contentTitle{
        margin-left:20px;
    }
}
@media screen and (max-width: 1200px){
    .contentListLeft{
        margin-left:20px;
    }
}
</style>



