import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import HomeOther from '@/components/home/HomeOther'
import Whisper from '@/components/whisper/whisper'
import Album from '@/components/album/album'
import About from '@/components/about/about'
import ContentList from '@/components/home/contentList'
import Detail from '@/components/home/details'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home,
      children: [
        {
          path: '/',
          component: HomeOther,
          children:[
            {
              path: '/',
              component: ContentList,
            },
            {
              path: '/contentList',
              component: ContentList,
            }
          ]
        },
        {
          path: '/whisper',
          component: Whisper
        },
        {
          path: '/album',
          component: Album
        },{
          path: '/about',
          component: About
        },{
          path: '/details',
          component: Detail
        }
      ]
    }
  ]
})



