<template>
    <div>
    <div v-for="(itemData ,index) in this.pageShowData" :key="index" v-show="showTab == index">
    <div class="contentList" v-for="(item ,index) in itemData" :key="index" >
        <div class="contentListLeft">
            <img :src="aLiYunUrl+item.images">
        </div>
        <div class="contentListRight">
            <h3>{{item.title}}</h3>
            <h5>{{item.kind}}</h5>
            <p>{{item.desc}}</p>
            <router-link :to="{path:'/details',query:{id:item.id}}">
                <span class="go-icon"></span>
            </router-link>
        </div>
    </div>
    <a-pagination :defaultCurrent="defaultCurrent" :total="len" class="page" :defaultPageSize="4" @change="pageChange" @showSizeChange="sizeChange" :current="defaultCurrent"/>
    </div>
    </div>
    <!-- images = models.ImageField(blank=True,verbose_name=u'图片')
    desc= models.TextField(max_length=200,verbose_name=u'简介')
    content = UEditorField(width=900, height=500, toolbars="full", imagePath="images/",filePath="files/",upload_settings = {"imageMaxSize": 1204000},settings = {}, verbose_name = '内容') -->

</template>

<script>
import axios from 'axios'
export default {
    name:'ContentList',
    props:['pageAlldata','pageShowData','len'],
    data() {
        return {
            aLiYunUrl: 'http://wangjiaanimals.oss-cn-shanghai.aliyuncs.com/media/',
            djangoLocalHost: 'http://127.0.0.1:8000',
            articleData:'',
            contentListSrc:'../../../static/img/sy_img1.jpg',
            flag: '',
            showData:this.pageShowData[0],
            defaultCurrent: 1,
            total:0,
            showTab:0
        }
    },
    beforeMount() {
        
        // console.log('bemounted --flag: '+this.pageAlldata)
        // this.pageShowData = this.pageAlldata[0]
    },
    mounted(){
        console.log("mounted")
        this.showData = this.pageShowData[0]
        console.log(this.pageShowData)
        // this.showData = this.pageAlldata
        // this.pageShowDataFun(this.showData)
        // localStorage.setItem('detailData',JSON.stringify(this.showData))
        // console.log(this.pageAlldata)
        // this.pageShowData= this.pageAlldata[0]
        // localStorage.setItem('detailData',JSON.stringify(this.pageShowData))
    },
    updated() {
        console.log("update")
    },
    methods: {
        pageChange(page,pageSize){
            this.showTab =page-1
            this.defaultCurrent = page
        },
        sizeChange(current,size){
            
            
        },
        pageShowDataFun(data,current){
            this.total = data.length
            const pages=[]
            data.forEach((item,index)=>{
                const page=Math.floor(index/4)
                if(!pages[page]){
                    pages[page]=[]
                }
                pages[page].push(item)
            });
            this.pageShowData =  pages    
        },
    },
    watch: {
        // flag(){
        //     console.log('flag  is   changed')
        // }
    },
    computed: {
        // kind(){
        //     return function(index){
        //         return this.pageAlldata[index]['kind'] == 'article' ? '设计文章' : this.pageShowData[index]['kind'] == 'frontend' ? '前端文章' : this.pageShowData[index]['kind'] == 'travel' ? '旅游杂记' :''
        //     }
        // },
    },
}
</script>

<style scoped>
.contentList{
    display:flex;
    margin-bottom:50px;
    /* flex-direction:row; */
}
.contentListLeft{
    flex:3;
    margin-left:0%;
}
.contentListLeft>img{
    width:90%
}
.contentListRight{
    flex:5;
    /* padding-right:20%; */
}
.contentListRight>p{
    /* overflow:hidden;
    line-height:30px;
    box-orient:vertical;
    line-clamp:2; */
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    /* autoprefixer: off */
    -webkit-box-orient: vertical;
    width:60%;
    line-height: 30px;
}
h3{
    display:flex;
    flex-direction:row;
    /* justify-content: center; */
    align-items:center;
    font-size:22px;
    line-height:40px;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
h3>button{
    margin-left:20px;
}
h5{
    color:#ff7f21;
    font-size:12px;
    padding:10px 0px;
}
.go-icon{
    width: 33px;
    height: 11px;
    background: url('../../../static/img/jiantou.png') center center no-repeat;
    display: block;
    padding: 10px 0;
    margin-top: 16px;
}
.page{
    display: flex;
    justify-content: center;
}
@media screen and (max-width: 768px){
    .contentTitle{
        margin-left:20px;
    }
    .contentListLeft{
        margin-left:20px;
    }
    .contentList{
        display:flex;
        flex-direction:column;
    }
    .contentListRight{
        margin-left:20px;
        padding-right:20px;
    }
    .contentListLeft>img{
        width:97%
    }

}
/* @media screen and (max-width: 1200px){
    .contentTitle{
        margin-left:20px;
    }
    .contentListLeft{
        margin-left:20px;
    }
} */
</style>



