<template>
    <div class="contentStyle">
        <div v-html="detailData">

        </div>
    </div>
</template>

<script>
export default {
    name:'detail',
    data() {
        return {
            detailData:[],
        }
    },
    mounted(){
        
        // if(!this.$store.state.detailData){
        //     console.log('this.$store.state.detailData ---undefined')
        //     this.$store.state.detailData=JSON.parse(localStorage.getItem('detailData'))
        // }
        console.log(this.$route.query.id)
        let content = this.detailData = JSON.parse(localStorage.getItem('detailData')).filter((item,index)=>{
            return item.id == this.$route.query.id
        })
        this.detailData = content[0]['content']
    }
}
</script>

<style scoped>
.contentStyle{
    padding:20px;
}
</style>



