<template>
    <div class="header">
       <div class="logo">
           <img :src="logoImg">
       </div>
       <div class="nav">
           <ul>
               <li><router-link to="/">文章</router-link></li>
               <li><router-link to="/whisper">微语</router-link></li>
               <li><router-link to="/album">相册</router-link></li>
               <li><router-link to="/about">关于</router-link></li>
           </ul>
       </div>
       <transition name="fadess" enter-active-class="animated fadeIn"
        leave-active-class="animated fadeOut"
        :duration="300">
            <div class="header-down-nav" v-show="show" @click="menulListClick">
                <ul>
                    <router-link to="/"> <li>文章</li></router-link>
                    <router-link to="/whisper"> <li>微语</li></router-link>
                    <router-link to="/album"> <li>相册</li></router-link>
                    <router-link to="/about"> <li>关于</li></router-link>
                </ul>
            </div>
       </transition>
       <div class="welcome-text">
           <p>欢迎来到<span class="name">FXY</span>的博客~</p>
       </div>
       <div class="menu-btn" @click="menuBtnClick">
            <div :class={menu:isMenu,on:menuOn} ></div>
       </div>
    </div>
</template>

<script>
export default {
    name:'homeHeader',
    data() {
        return {
            logoImg:'static/img/logo.png',
            show:false,
            isMenu:true,
            menuOn:false,
        }
    },
    methods: {
        menuBtnClick(){
            this.show=!this.show;
            this.menuOn=!this.menuOn;
        },
        menulListClick(e){
            console.log(e);
            this.show= false;
        }
    },
}
</script>

<style scoped>
@import '../../../static/css/animate.css';
.header{
    height:120px;
    /* background: red; */
    display: flex;
    font-size: 16px;
}
.logo{
    display:inline-block;
    height:100%;
    width:80px;
    margin-left:20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex:1;
}
.nav{
    display: inline-block;
    flex:3;
    margin-left:50px;
    /* margin */
}
.header-down-nav{
    width:100%;
}
.nav>ul{
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.nav>ul>li{
    width:80px;
    text-align: center;
    cursor: pointer;
}
.nav>ul>li>a{
    text-decoration: none;
    color:#3f2863;
}
.header-down-nav{
    position: absolute;
    /* display: none; */
    top:120px;
    z-index: 100;
}
.header-down-nav>ul{
    /* text-align: center; */
    background: #ff7f21;
}
.header-down-nav>ul li{
    /* padding:10px 0px; */
    line-height:60px;
    font-size: 16px;
    padding-left:40px;
    cursor: pointer; 
}
.header-down-nav>ul a{
    text-decoration:none;
    color:rgba(255,255,255,.7);
}
.header-down-nav>ul a li:hover{
    opacity: .2;
    background: #409EFF;
}
.welcome-text{
    flex:8;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    margin-right:40px;
}
@media screen and (max-width: 768px){
  .header>.nav{
      display: none;
  }
  /* .header-down-nav{display: block;} */
  .welcome-text{visibility: hidden;}
  .menu { width: 50px; height: 40px; position: relative; cursor: pointer;}
  .menu:before, .menu:after { content: ""; display: block; width: 50px; height: 6px; background: #ff7f21; border-radius: 8px; position: absolute; left: 0; -webkit-transition: all 0.15s ease-in-out; transition: all 0.15s ease-in-out; }
  .menu:before { top: 0px; box-shadow: 0 34px #ff7f21;}
  .menu:after { bottom: 17px; }
  .menu.on:before { top: 17px; box-shadow: none; -webkit-transform: rotate(225deg); transform: rotate(225deg); }
  .menu.on:after { bottom: 17px; -webkit-transform: rotate(135deg); transform: rotate(135deg); }
  .menu-btn{position: absolute; top: 40px; right: 20px;}
}
/* .menu { width: 50px; height: 40px; position: relative; cursor: pointer;}
.menu:before, .menu:after { content: ""; display: block; width: 50px; height: 6px; background: #ff7f21; border-radius: 8px; position: absolute; left: 0; -webkit-transition: all 0.15s ease-in-out; transition: all 0.15s ease-in-out; }
.menu:before { top: 0px; box-shadow: 0 34px #ff7f21;}
.menu:after { bottom: 17px; }
.menu.on:before { top: 17px; box-shadow: none; -webkit-transform: rotate(225deg); transform: rotate(225deg); }
.menu.on:after { bottom: 17px; -webkit-transform: rotate(135deg); transform: rotate(135deg); }
.menu-btn{position: absolute; top: 40px; right: 20px;} */
</style>



