<template>
<div class="content whisper-content">
    <div class="cont">
      <div class="whisper-list">
        <div class="item-box" v-for="(sonItem ,index) in pageShowWhieperData" :key="index">
          <div class="item">
            <div class="whisper-title">
              <i class="layui-icon layui-icon-date"></i><span class="hour">{{sonItem.hour}}</span><span class="date">{{sonItem.data}}</span>
            </div>
            <p class="text-cont">
              {{sonItem.textCont}}
            </p>
            <div class="img-box" v-show="imgBoxImgLen(sonItem)== 1">
              <img :src="sonItem.imgBoxImg[0]">
            </div>
            <div class="img-box" v-show="imgBoxImgLen(sonItem)== 2">
              <img :src="sonItem.imgBoxImg[0]">
              <img :src="sonItem.imgBoxImg[1]">
            </div>
            <div class="img-box" v-show="imgBoxImgLen(sonItem)== 3">
              <img :src="sonItem.imgBoxImg[0]">
              <img :src="sonItem.imgBoxImg[1]">
              <img :src="sonItem.imgBoxImg[2]">
            </div>
            <div class="img-box" v-show="imgBoxImgLen(sonItem)== 4">
              <img :src="sonItem.imgBoxImg[0]">
              <img :src="sonItem.imgBoxImg[1]">
              <img :src="sonItem.imgBoxImg[2]">
              <img :src="sonItem.imgBoxImg[3]">
            </div>
            <!-- <div class="img-box" v-show="wy_img1">
              <img :src="sonItem.imgBoxImg[1]">
              <img :src="sonItem.imgBoxImg[2]">
            </div>
            <div class="img-box" v-show="wy_img1">
              <img :src="sonItem.imgBoxImg[0]">
              <img :src="sonItem.imgBoxImg[1]">
              <img :src="sonItem.imgBoxImg[2]">
            </div> -->
            <div class="op-list">
              <!-- <p class="like"><a-icon type="heart" /><span>1200</span></p> 
              <p class="edit"><i class="el-icon-star-off"></i><span>1200</span></p> -->
              <!-- <p class="off"><span>展开</span><a-icon type="down" /></p> -->
            </div>
          </div>
        </div>
      </div>
      <div id="demo" style="text-align: center;"></div>
      <a-pagination :defaultCurrent="defaultCurrent" :current="defaultCurrent" :total="total" class="page" :defaultPageSize="4" @change="pageChange"/>
    </div>
  </div>
</template>

<script>
import HomeHeader from '@/components/home/HomeHeader'
import axios from 'axios'
export default {
    name:'whisper',
    data() {
        return {
            wy_img1: '../../../static/img/wy_img1.jpg',
            header2: '../../../static/img/header2.png',
            header: '../../../static/img/header.png',
            aLiYunUrl: 'http://wangjiaanimals.oss-cn-shanghai.aliyuncs.com/media/',
            allWhisper:[],
            newWhisper:[],
            pageWhisper:[],
            itemBox:[],
            pageShowWhieperData:[],
            total:0,
            defaultCurrent: 1,
        }
    },
    components:{
        HomeHeader
    },
    mounted() {
      this.getAllWhieper()
      .then((res)=>{
        if(res.status == 200){
          this.allWhisper= res.data
          this.allWhisper.forEach((item,index)=>{
            console.log(item)
            let obj={
              data: item.dataTime.slice(0,10),
              hour: item.dataTime.slice(11),
              textCont: item.desc,
              imgBoxImg :[]
            }
            if(item.imgUrl1){
              obj.imgBoxImg.push(this.aLiYunUrl+item.imgUrl1)
            }
            if(item.imgUrl2){
              obj.imgBoxImg.push(this.aLiYunUrl+item.imgUrl2)
            }
            if(item.imgUrl3){
              obj.imgBoxImg.push(this.aLiYunUrl+item.imgUrl3)
            }
            if(item.imgUrl4){
              obj.imgBoxImg.push(this.aLiYunUrl+item.imgUrl4)
            }
            this.newWhisper.push(obj)
            this.total= this.newWhisper.length
            this.pageShowDataFun(this.newWhisper)
            this.pageShowWhieperData = this.pageWhisper[0]
          })
        }
      })
      .catch((err)=>{

      })
    },
    methods: {
      getAllWhieper(){
        return axios.get('/fxy/allWhisper')
      },
      pageShowDataFun(data,key){
        const pages=[]
        data.forEach((item,index)=>{
            
            const page=Math.floor(index/4)
            if(!pages[page]){
                pages[page]=[]
            }
            pages[page].push(item)
        });
        this.pageWhisper = pages
      },
       pageChange(page,pageSize){
            this.pageShowWhieperData = this.pageWhisper[page-1]
            this.defaultCurrent = page
        },
    },
    computed: {
      imgBoxImgLen:function(){
       return function (item) {
          console.log(item.imgBoxImg.length)
          return item.imgBoxImg.length;
       }
        // return item.imgBoxImg.length;
      }
    },
}
</script>

<style scoped>
/*whisper*/
.whisper-content{padding:40px 0 100px 0;}
.whisper-content .item-box{background: #f9f9f9; margin-bottom: 20px;}
.whisper-content .cont{max-width: 800px; margin:0 auto;}
.whisper-content .cont .item-box .item{padding:50px 46px 20px 54px;}
.whisper-content .cont .whisper-list .whisper-title{height: 20px; line-height: 20px; margin-bottom: 20px;}
.whisper-content .cont .whisper-list .whisper-title i{position: relative; top: 1px; margin-right: 5px;}
.whisper-content .cont .whisper-list .whisper-title span{color: #ff7f21;}
.whisper-content .cont .whisper-list .whisper-title span.hour{margin-right: 20px;}
.whisper-content .whisper-list .text-cont{line-height: 24px; margin-bottom: 28px;}
.whisper-content .whisper-list .op-list{margin-top: 30px; overflow: hidden;}
.whisper-content .whisper-list .op-list .like{ -webkit-user-select: none;
-moz-user-focus: none;
-moz-user-select: none;}
.tlinks {
  text-indent: -9999px;
  height: 0;
  line-height: 0;
  font-size: 0;
  overflow: hidden;
}
.whisper-content .whisper-list .op-list .like.active{color: #ff7f21;}
.whisper-content .whisper-list .op-list p{float: left; cursor: pointer; color: #999999;}
.whisper-content .whisper-list .op-list p.off{float: right; -webkit-user-select: none;
-moz-user-focus: none;
-moz-user-select: none;}
.whisper-content .whisper-list .op-list p.off span{margin-right: 5px;}
.whisper-content .whisper-list .op-list p.like{margin-right: 50px;}
.whisper-content .whisper-list .img-box{overflow: hidden;}
.whisper-content .whisper-list .img-box img{float: left; margin: 0px 18px 18px 0;}
.whisper-content .whisper-list .img-box img:last-child{margin-right: 0;}
/*end-whisper*/
.page{
    display: flex;
    justify-content: center;
}
.img-box{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}
.img-box>img{
  width:30%;
}
</style>



