import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const store = new Vuex.Store({
    state:{
        detailData:'',
        flag: 'article',
        pageAlldata:null,
        defaultCurrent:1,
        defaultKey: 'article'
    },
    mutations:{
        changeFlag(state,Flag){
            state.flag = Flag
        },
        detailData(state,data){
            state.detailData = data
        },
        pageAlldata(state,data){
            state.pageAlldata = data
        },
        changeDefaultKey(state,key){
            state.defaultKey = key
            // alert(state.defaultKey)
        },
        changeDefaultCurrent(state,key){
            state.defaultCurrent = key
        }
    },
    actions:{
       getChangeFlag(context,Flag){
           context.commit('changeFlag',Flag)
       },
       getDetailData(context,Data){
        context.commit('detailData',Data)
       },
       getPageAlldata(context,data){
        context.commit('pageAlldata',data)
       },
       getDefaultKey(context,key){
           context.commit('changeDefaultKey',key)
       },
       getDefaultCurrent(context,key){
           context.commit('changeDefaultCurrent',key)
       }
    }
})


export default store;


