<template>
<div class="album-content w1000">
  <div v-for="(item,index) in pageShowAlbumData" :key="index">
    <div class="img-info">
      <img :src="aLiYunUrl+item.imgUrl" alt="">
      <div class="title">
        <p class="data">今日上传<span>{{item.data}}</span></p>
        <p class="text">{{item.title}}</p>
      </div>
    </div>
  </div>
  <a-pagination :defaultCurrent="defaultCurrent" :current="defaultCurrent" :total="total" class="page" :defaultPageSize="4" @change="pageChange"/>
</div>
</template>

<script>
import HomeHeader from '@/components/home/HomeHeader'
import axios from 'axios'
export default {
    name:'album',
    data() {
        return {
            xc_img1: '../../../static/img/xc_img1.jpg',
            xc_img3: '../../../static/img/xc_img3.jpg',
            aLiYunUrl: 'http://wangjiaanimals.oss-cn-shanghai.aliyuncs.com/media/',
            allAlbumData:null,
            pageAlbum:null,
            defaultCurrent:1,
            pageShowAlbumData:null,
            total:0
        }
    },
    components:{
        HomeHeader
    },
    methods: {
      getAlbum(){
        return axios.get('/fxy/allAlbum')
      },
      pageShowDataFun(data,key){
        const pages=[]
        data.forEach((item,index)=>{
            
            const page=Math.floor(index/4)
            if(!pages[page]){
                pages[page]=[]
            }
            pages[page].push(item)
        });
        this.pageAlbum = pages
      },
      pageChange(page,pageSize){
        this.pageShowAlbumData = this.pageAlbum[page-1]
        this.defaultCurrent = page
      },
    },
    mounted() {
      this.getAlbum()
      .then((res)=>{
        if(res.status == 200){
          this.allAlbumData= res.data
          this.pageShowDataFun(this.allAlbumData)
          this.pageShowAlbumData = this.pageAlbum[0]
          this.total = this.allAlbumData.length
        }
      })
      .catch((err)=>{

      })
    },
}
</script>

<style scoped>
/*album*/
.w1000{max-width: 1000px!important; margin:0 auto;}
.album-content .img-info{margin-top: 66px; overflow: hidden; font-size: 16px; margin-bottom: 126px;}
.album-content .img-info img{float: left; padding-right: 60px;}
.album-content .img-info .title{line-height: 30px; margin-top: 124px; padding-left: 20px;}
.album-content .img-info .title .data{color: #ff7f21;}
.album-content .img-info .title .data span{padding-left: 35px;}
.album-content .img-list{overflow: hidden; padding-bottom: 50px;}
.album-content .img-list .item{float: left; width: 100%;}
.album-content .img-list .layui-laypage{margin: 50px 0;}
.album-content .img-list .space>*{padding: 55px;}
.album-content .img-list .layui-col-space80{padding: 40px;}
.album-content .img-list .item img{width: 100%;}
.album-content .img-list .cont-text{padding-left: 10px;}
.album-content .img-list .cont-text .data{line-height: 55px; color: #ff7f21;}
.album-content .img-list .cont-text .briefly{line-height: 24px; margin-top: 16px;}
/*end-album*/
.page{
    display: flex;
    justify-content: center;
}
@media screen and (max-width: 1000px){
  .leacots-content{padding:0 20px 40px 20px;}
  .album-content .img-info img{width: 100%; padding-right: 0; margin-bottom: 20px;}
  .album-content .img-info{margin-top: 0px; margin-bottom: 50px;}
  .album-content .img-list .space>*{padding:30px;}
}
.el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
</style>



